import * as React from 'react'
import _ from 'lodash'
import Relation from './Relation'
import { simulate, nodeArrToMap, linkArrToMap } from './util'

import { Links, Nodes, SimulateOptions } from './types'

interface Props {
  data: {
    nodes: Nodes
    links: Links<string>
  }
  simulate: SimulateOptions
  style: {
    backgroundColor: string
    radius: number
  }
  tooltip?: (data: any) => JSX.Element
  onNodeClick?: (n: Node) => void
}

interface State {
  data: {
    nodes: Nodes
    links: Links<Node>
  }
}

class RelationMap extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props)

    this.state = {
      data: {
        nodes: [],
        links: []
      }
    }
  }

  componentDidMount() {
    this.genLocation(this.props)
  }

  componentWillReceiveProps(nextProps: Props) {
    const isSame = this.isSameId(this.props.data.nodes, nextProps.data.nodes)

    if (isSame) {
      this.mergePreData(nextProps.data)
    } else {
      this.genLocation(nextProps)
    }
  }

  isSameId = (preNodes: Nodes, nextNodes: Nodes) => {
    const preIds = preNodes.map(node => node.id)
    const nextIds = nextNodes.map(node => node.id)

    return nextIds.every(id => preIds.includes(id))
  }

  genLocation = async (props: Props) => {
    const { data } = props

    const nextData = await simulate(_.cloneDeep(data), props.simulate)

    this.setState({
      data: nextData
    })
  }

  mergePreData = (nextData: { nodes: Nodes; links: Links<string> }) => {
    const preData = this.state.data

    const preNodesMap = nodeArrToMap(preData.nodes)
    const preLinksMap = linkArrToMap(preData.links)

    const nextStateNodes = nextData.nodes.map(node => {
      return _.merge({}, preNodesMap[node.id], node)
    })

    const _nodesMap = nodeArrToMap(nextStateNodes)

    const nextStateLinks = nextData.links.map(link => {
      const preLink = preLinksMap[link.id] || {}

      return _.merge({}, preLink, link, {
        source: _nodesMap[link.source],
        target: _nodesMap[link.target]
      })
    })

    this.setState({
      data: {
        nodes: nextStateNodes as Nodes,
        links: nextStateLinks as any
      }
    })
  }

  setNextData = (data: any) => {
    this.setState({ data })
  }

  render() {
    const { data } = this.state
    const otherProps = _.omit(this.props, 'data')

    return (
      <Relation data={data} setNextData={this.setNextData} {...otherProps} />
    )
  }
}

export default RelationMap
