import * as React from 'react'
import { render } from 'react-dom'
import Relation from '../src'
import bunny from './asserts/bunny.png'
import _ from 'lodash'

const node0Style = {
  backgroundColor: 'gray',
  backgroundUrl: bunny,
  radius: 15
}

const node1Style = {
  radius: 10,
  backgroundColor: '#CA8622'
}

const node2Style = {
  radius: 10,
  backgroundColor: '#C23531'
}

const node3Style = {
  radius: 10,
  backgroundColor: '#000'
}

const lineStyle = {
  width: 1,
  color: 'grey'
}

const nodes = [
  { id: 'c1', style: node0Style },
  { id: 'c2', style: node0Style },
  { id: 'c3', style: node0Style }
] as any

const links = [] as any

for (let i = 0; i < 10; i++) {
  nodes.push({
    id: String(i),
    style: node1Style
  })

  links.push({
    source: 'c1',
    target: String(i),
    id: String(i),
    style: lineStyle
  })
}

for (let i = 10; i < 20; i++) {
  nodes.push({
    id: String(i),
    style: node2Style
  })

  links.push({
    source: 'c2',
    target: String(i),
    id: String(i),
    style: lineStyle
  })
}

for (let i = 20; i < 30; i++) {
  nodes.push({
    id: String(i),
    style: node3Style
  })

  links.push({
    source: 'c3',
    target: String(i),
    id: String(i),
    style: lineStyle
  })
}

class App extends React.Component<any, any> {
  constructor(props: any) {
    super(props)

    this.state = {
      nodes,
      links
    }
  }

  render() {
    const { nodes, links } = this.state

    return (
      <>
        <div style={{ height: 800 }}>
          <Relation
            data={{
              nodes,
              links
            }}
            style={{
              backgroundColor: '#F3F3F3',
              radius: 10
            }}
            simulate={{
              width: 1000,
              height: 800,
              collisionRadius: 30,
              strength: 0.1
            }}
            tooltip={(data: any) => {
              return <div>this is node {data.id}</div>
            }}
          />
        </div>
      </>
    )
  }
}

render(<App />, document.getElementById('root'))
