import * as React from 'react'
import { Group } from '@vx/group'
import { localPoint } from '@vx/event'
import { Node } from './types'
import { CircleClipPath } from '@vx/clip-path'

interface Props {
  onNodeClick?: Function
  showTooltip: Function
  hideTooltip: Function
  dragStart: (event: any) => void
  dragEnd: (event: any) => void
  dragMove: (event: any) => void
  isDragging: boolean
  node: Node
}

export default class CusNode extends React.Component<Props> {
  onMouseOver = (event: any) => {
    const { showTooltip, node } = this.props

    const coords = localPoint(event.target.ownerSVGElement, event)

    showTooltip({
      tooltipLeft: coords.x,
      tooltipTop: coords.y,
      tooltipData: node
    })
  }

  onMouseOut = () => {
    this.props.hideTooltip()
  }

  render() {
    const {
      node,
      dragEnd,
      dragStart,
      dragMove,
      onNodeClick,
      isDragging
    } = this.props
    const { radius, backgroundColor, backgroundUrl } = node.style

    const isImg = !!backgroundUrl

    const events = {
      onMouseMove: dragMove,
      onMouseUp: (event: any) => {
        dragEnd(event)

        if (!isDragging) {
          onNodeClick && onNodeClick(node)
        }
      },
      onMouseDown: dragStart,
      onMouseOver: this.onMouseOver,
      onMouseOut: this.onMouseOut
    }

    return (
      <Group transform={`translate(${node.x}, ${node.y})`}>
        {/* img clip */}
        <CircleClipPath id={`circle-${node.id}`} cx={0} cy={0} r={radius} />
        {/* circle */}
        <circle r={radius} fill={backgroundColor} cx={0} cy={0} {...events} />
        {/* img */}
        {isImg && (
          <image
            xlinkHref={backgroundUrl}
            width={radius * 2}
            height={radius * 2}
            x={-radius}
            y={-radius}
            clipPath={`url(#circle-${node.id})`}
            style={{ fill: backgroundColor }}
            {...events}
          />
        )}
      </Group>
    )
  }
}
