/**
 * Relation map
 */
import * as React from 'react'
import { Group } from '@vx/group'
import { withTooltip, TooltipWithBounds } from '@vx/tooltip'
import { withParentSize } from '@vx/responsive'
import { localPoint } from '@vx/event'
import { Zoom } from '@vx/zoom'
import _ from 'lodash'
import { Drag } from '@vx/drag'
import CusLine from './CusLine'
import CusNode from './CusNode'
import Bg from './Bg'
import { createSimulate } from './util'

import { Links, Nodes, Node, SimulateOptions } from './types'

interface Props {
  data: {
    nodes: Nodes
    links: Links<Node>
  }
  setNextData: Function
  style: {
    backgroundColor: string
    radius: number
  }
  parentWidth: number
  parentHeight: number
  tooltipData: any
  tooltipLeft: number
  tooltipTop: number
  tooltipOpen: boolean
  showTooltip: Function
  hideTooltip: Function
  simulate: SimulateOptions
  tooltip?: (data: any) => any
  onNodeClick?: (node: Node) => void
}

interface State {
  activeNode: any
  activeLink: any
  highlightLinks: any[]
  highlightNodes: any[]
  dragInfo: any
}

// zoom init config
const initialTransform = {
  scaleX: 1,
  scaleY: 1,
  translateX: 0,
  translateY: 0,
  skewX: 0,
  skewY: 0
}

class Relation extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props)

    this.state = {
      activeNode: {},
      activeLink: {},
      highlightLinks: [],
      highlightNodes: [],
      dragInfo: {}
    }
  }

  sim: any = null

  disActive = () => {
    this.setState({
      activeNode: {},
      activeLink: {},
      highlightLinks: [],
      highlightNodes: []
    })

    this.props.hideTooltip()
  }

  onDragStart = (node: Node) => {
    const { dragInfo } = this.state

    this.setState({
      dragInfo: {
        ...dragInfo,
        node,
        x: node.x,
        y: node.y
      }
    })
  }

  onDragMove = (dragState: any, zoom: any) => {
    const { dragInfo } = this.state
    const { data, setNextData, simulate } = this.props

    if (!dragInfo.isDragging) {
      const simData = {
        nodes: data.nodes,
        links: data.links.map(l => ({
          ...l,
          target: l.target.id,
          source: l.source.id
        }))
      }

      this.sim = createSimulate(simData, simulate, () => {
        setNextData(simData)
      })
    }

    const scale = zoom.transformMatrix.scaleX
    const { dx, dy } = dragState

    const node = data.nodes.find(n => n.id === dragInfo.node.id)

    node.fx = dragInfo.x + dx / scale
    node.fy = dragInfo.y + dy / scale

    this.setState({
      dragInfo: {
        ...dragInfo,
        isDragging: true
      }
    })
  }

  onDragEnd = () => {
    const { dragInfo } = this.state
    const { data } = this.props

    if (this.sim) {
      this.sim.stop()
    }

    const node = data.nodes.find(n => n.id === dragInfo.node.id)

    node.fx = null
    node.fy = null

    this.setState({
      dragInfo: {
        node: null
      }
    })
  }

  rendetTooltip = () => {
    const {
      tooltip,
      tooltipOpen,
      tooltipLeft,
      tooltipTop,
      tooltipData
    } = this.props

    if (!tooltip || !tooltipOpen) {
      return null
    }

    return (
      <TooltipWithBounds
        key={Math.random()}
        left={tooltipLeft}
        top={tooltipTop}
      >
        {tooltip(tooltipData)}
      </TooltipWithBounds>
    )
  }

  render() {
    const { dragInfo } = this.state
    const {
      onNodeClick,
      showTooltip,
      hideTooltip,
      parentWidth,
      parentHeight,
      data,
      style
    } = this.props

    const width = parentWidth
    const height = parentHeight

    return (
      <React.Fragment>
        <Zoom
          width={width}
          height={height}
          scaleXMin={1 / 10}
          scaleXMax={100}
          scaleYMin={1 / 10}
          scaleYMax={100}
          transformMatrix={initialTransform}
        >
          {(zoom: any) => {
            const events = {
              onWheel: zoom.handleWheel,
              onMouseDown: zoom.dragStart,
              onMouseMove: zoom.dragMove,
              onMouseUp: zoom.dragEnd,
              onMouseLeave: () => {
                if (!zoom.isDragging) {
                  return
                }
                zoom.dragEnd()
              },
              onDoubleClick: (event: any) => {
                const point = localPoint(event)
                zoom.scale({ scaleX: 1.1, scaleY: 1.1, point })
              }
            }

            return (
              <div style={{ position: 'relative', width, height }}>
                <svg width={width} height={height}>
                  <Bg
                    width={width}
                    height={height}
                    from={style.backgroundColor}
                    to={style.backgroundColor}
                    rx={style.radius}
                  />
                  <rect
                    fill="transparent"
                    height={height}
                    width={width}
                    {...events}
                  />
                  <Group transform={zoom.toString()}>
                    {data.links.map(link => (
                      <CusLine key={link.id} link={link} />
                    ))}
                    {data.nodes.map(node => (
                      <Drag
                        key={node.id}
                        width={width}
                        height={height}
                        onDragStart={() => this.onDragStart(node)}
                        onDragMove={(dragState: any) =>
                          this.onDragMove(dragState, zoom)
                        }
                        onDragEnd={this.onDragEnd}
                        resetOnStart={true}
                      >
                        {({ dragStart, dragEnd, dragMove }: any) => (
                          <CusNode
                            key={node.id}
                            node={node}
                            showTooltip={showTooltip}
                            hideTooltip={hideTooltip}
                            onNodeClick={onNodeClick}
                            dragStart={dragStart}
                            dragEnd={dragEnd}
                            dragMove={dragMove}
                            isDragging={dragInfo.isDragging}
                          />
                        )}
                      </Drag>
                    ))}
                  </Group>
                </svg>
              </div>
            )
          }}
        </Zoom>
        {this.rendetTooltip()}
      </React.Fragment>
    )
  }
}

const RelationWithPlugin = withParentSize(withTooltip(Relation))

export default RelationWithPlugin
