/**
 * bg
 */
import * as React from 'react'
import { LinearGradient } from '@vx/gradient'

interface Props {
  width: number
  height: number
  from: string
  to: string
  rx: number
}

export default (props: Props) => {
  const { width, height, from = '#fff', to = '#fff', rx } = props

  return (
    <>
      <LinearGradient id="bg" from={from} to={to} />
      <rect width={width} height={height} fill={'url(#bg)'} rx={rx} />
    </>
  )
}
