import _ from 'lodash'
import * as d3 from 'd3'

import {
  Nodes,
  Links,
  SimulateOptions,
  Location,
  NodeMap,
  LinkMap
} from './types'

/**
 * node arr to node map
 * @param nodes
 */
export function nodeArrToMap(nodes: Nodes): NodeMap {
  return nodes.reduce(
    (obj, node) => {
      obj[node.id] = node
      return obj
    },
    {} as NodeMap
  )
}

/**
 * link arr to node map
 */
export function linkArrToMap(links: Links<any>): LinkMap {
  return links.reduce(
    (obj, link) => {
      obj[link.id] = link
      return obj
    },
    {} as LinkMap
  )
}

export function linkNodeById(nodes: Nodes, links: Links<string>) {
  const nodesMap = nodeArrToMap(nodes)

  return links.map(link => {
    const st = {
      source: nodesMap[link.source]
    }

    return _.merge(link)
  })
}

/**
 * use d3 simulate cal point location
 * @param data
 * @param opt
 */
export function simulate(
  data: {
    nodes: Nodes
    links: Links<string>
  },
  opt: SimulateOptions
): Promise<{ nodes: Nodes; links: Links<Node> }> {
  const { nodes, links } = data
  const { width, height, collisionRadius, strength } = opt

  return new Promise(resolve => {
    const simulation = d3
      .forceSimulation()
      .nodes(nodes)
      .force('links', d3.forceLink(links).id((d: any) => d.id))
      .force('center_force', d3.forceCenter(width / 2, height / 2))
      .force('collide', d3.forceCollide(collisionRadius))
      .force('x', d3.forceX(width / 2).strength(strength))
      .force('y', d3.forceY(height / 2).strength(strength))
      .stop()

    const num = Math.ceil(
      Math.log(simulation.alphaMin()) / Math.log(1 - simulation.alphaDecay())
    )

    for (let i = 0; i < num; ++i) {
      simulation.tick()
    }

    const nextData = {
      nodes,
      links
    }

    resolve(nextData as any)
  })
}

export const createSimulate = (
  data: {
    nodes: Nodes
    links: Links<string>
  },
  opt: SimulateOptions,
  cb: () => void
) => {
  const { nodes, links } = data
  const { width, height, collisionRadius, strength } = opt

  const cb_throttle = _.throttle(cb, 1000 / 24) // 24帧

  const simulation = d3
    .forceSimulation()
    .nodes(nodes)
    .force('links', d3.forceLink(links).id((d: any) => d.id))
    .force('collide', d3.forceCollide(collisionRadius))
    .force('x', d3.forceX(width / 2).strength(strength))
    .force('y', d3.forceY(height / 2).strength(strength))
    .on('tick', cb_throttle)

  return simulation
}

/**
 * cal link start and end point
 * @param startP
 * @param endP
 * @param startR
 * @param endR
 */
export function getSE(
  startP: Location,
  endP: Location,
  startR: number,
  endR: number
): [Location, Location] {
  const distance = Math.sqrt(
    Math.pow(startP.x - endP.x, 2) + Math.pow(startP.y - endP.y, 2)
  )

  const h = Math.abs(startP.y - endP.y)

  const sh = (h * startR) / distance
  const sw = Math.sqrt(Math.pow(startR, 2) - Math.pow(sh, 2))

  const eh = (h * (distance - endR)) / distance
  const ew = Math.sqrt(Math.pow(distance - endR, 2) - Math.pow(eh, 2))

  const _startP = { x: 0, y: 0 }
  const _endP = { x: 0, y: 0 }

  if (endP.x - startP.x > 0) {
    _startP.x = startP.x + sw
    _endP.x = startP.x + ew
  } else {
    _startP.x = startP.x - sw
    _endP.x = startP.x - ew
  }

  if (endP.y - startP.y > 0) {
    _startP.y = startP.y + sh
    _endP.y = startP.y + eh
  } else {
    _startP.y = startP.y - sh
    _endP.y = startP.y - eh
  }

  return [_startP, _endP]
}

/**Location
 * cal text angle
 */
export function calAngle(start: Location, end: Location): number {
  const dx = end.x - start.x
  const dy = end.y - start.y

  const radians = Math.atan2(dy, dx)

  return radians * (180 / Math.PI)
}
