import * as React from 'react'
import { Group } from '@vx/group'
import { getSE, calAngle } from './util'
import { Text } from '@vx/text'

import { Link, Node, Location } from './types'

interface Props {
  link: Link<Node>
}

export default (props: Props) => {
  const { link } = props
  const { text, textStyle, style } = link

  const linkStyle = {
    color: '#0000FF',
    width: 1,
    ...style
  }

  const linkTextStyle = {
    fontSize: '10px',
    color: '#fff',
    ...textStyle
  }

  const offset = 1 // 箭头偏移量

  const [start, end] = getSE(
    link.source as Location,
    link.target as Location,
    link.source.style.radius,
    link.target.style.radius + offset
  )

  const angle = start.x < end.x ? calAngle(start, end) : calAngle(end, start)

  return (
    <Group>
      <marker
        id={link.id}
        viewBox="0 -5 10 10"
        refX={10 - offset * 3}
        refY={0}
        markerWidth="4"
        markerHeight="4"
        orient="auto"
        fill={linkStyle.color}
      >
        <path d="M0,-5L10,0L0,5" />
      </marker>
      {text && (
        <Text
          textAnchor="middle"
          verticalAnchor={'end'}
          x={(start.x + end.x) / 2}
          y={(start.y + end.y) / 2}
          angle={angle}
          style={linkTextStyle}
        >
          {text}
        </Text>
      )}
      <line
        x1={start.x}
        y1={start.y}
        x2={end.x}
        y2={end.y}
        strokeWidth={linkStyle.width}
        stroke={linkStyle.color}
        markerEnd={`url(#${link.id})`}
      />
    </Group>
  )
}
